* {
  box-sizing: border-box;
}

.wrapper {
  background: #074199;
  justify-content: center;
  display: flex;
}

.search-form {
  width: 400px;
  margin: 1em;
  position: relative;
}

.search-btn {
  width: 35px;
  height: 100%;
  border: 1px solid;
  border-left-style: none;
  border-radius: 0 5px 5px 0;
  position: absolute;
  top: 0;
  right: 0;
}

.search-input {
  width: 100%;
  height: 3em;
  border: 1px solid;
  border-radius: 5px;
  padding: 0 1.5em;
  line-height: 3em;
  display: block;
}

